import { RongIms } from './RongIM'

export const connectMessage = {
    init(obj) {//
        RongIms.init(obj)
    },
    connect(token) {//token
        RongIms.connect(token);
    },
    message(callback) {//
        if (callback) {
            RongIms.message(callback);
        }
    }

}


